		  console.log(res.errCode);
		});
	},
	onUnload() {
		bgcAudioContext.pause();
	},
	methods:{
		start(){
			this.num++;
			this.startShou();
			this.startGong();
			bgcAudioContext.play();
			setTimeout(()=>{
				if(this.num%2==0){
					this.isLeft=true;
					setTimeout(()=>{
						this.positionArrDog=[];
						for (let i = 0; i <this.dogData.length; i++) {
							this.positionArrDog.push([100,1000]);
						}
						this.positionArrCat=[];
						for (let i = 0; i <this.catData.length; i++) {
							this.positionArrCat.push([100,1000]);
						}	
						this.isStartS=!this.isStartS;
						this.isStartG=!this.isStartG;
						this.isLeft=false;
					},2000)
				}else{
					this.isRight=true;
					setTimeout(()=>{
						this.positionArrDog=[];
						for (let i = 0; i <this.dogData.length; i++) {
							this.positionArrDog.push([100,1000]);
						}
						this.positionArrCat=[];
						for (let i = 0; i <this.catData.length; i++) {
							this.positionArrCat.push([100,1000]);
						}	
						this.isStartS=!this.isStartS;
						this.isStartG=!this.isStartG;
						this.isRight=false;
					},2000)
				}
				bgcAudioContext.pause();
				faliedAudioContext.play();
			},6000)
		},
		startShou(){
			if(this.isStartS){
				this.positionArrDog=[];
				for (let i = 0; i <this.dogData.length; i++) {
					this.positionArrDog.push([i%3*70,Math.floor(i/3)*90]);
				}
			}else{
				this.positionArrDog=[];
				for (let i = 0; i <this.dogData.length; i++) {
					this.positionArrDog.push([100,1000]);
				}
			}
			this.isStartS=!this.isStartS;
		},
		startGong(){
			if(this.isStartG){
				this.positionArrCat=[];
				for (let i = 0; i <this.catData.length; i++) {
					this.positionArrCat.push([i%3*70,Math.floor(i/3)*90]);
				}	
			}else{
				this.positionArrCat=[];
				for (let i = 0; i <this.catData.length; i++) {
					this.positionArrCat.push([100,1000]);
				}	
			}
			this.isStartG=!this.isStartG;
		},
	}
